<template>
  <div>
    <mu-container style="padding:0;background:#f1eff2">
      <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
        <a @click="()=>{
          $router.go(-1)
        }">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
        <span style="color:white">维护维修</span>
      </mu-appbar>-->
      <van-nav-bar
        :fixed="true"
        title="维护维修"
        @click-left="()=>{
          $router.go(-1)
         }"
        left-arrow
        class="backgroundNaviBar"
        z-index="999"
        :border="false"
      >
        <!-- <van-icon name="search" slot="right" /> -->
      </van-nav-bar>
      <mu-tabs
        :value.sync="active2"
        :indicator-color="$store.state.color"
        inverse
        :color="$store.state.color"
        full-width
        class="ChildtabBtnStyles"
      >
        <mu-tab class="btnHeaderStyles">例行巡查</mu-tab>
        <mu-tab class="btnHeaderStyles">保养检修</mu-tab>
        <mu-tab class="btnHeaderStyles">故障维修</mu-tab>
      </mu-tabs>
      <div class="demo-text" v-if="active2 === 0" style="margin-top:83px;">
        <div style>
          <MaintenanceChildInspection :type="Xtype" :station="XstationName" :taskId="XtaskId" />
        </div>
      </div>
      <div class="demo-text" v-if="active2 === 1" style="margin-top:83px;">
        <MaintenanceChild2 :type="Jtype" :station="JstationName" :taskId="JtaskId" />
      </div>
      <div class="demo-text" v-if="active2 === 2" style="margin-top:83px;">
        <MaintenanceChildControl :type="Wtype" :station="WstationName" :taskId="WtaskId" />
      </div>
    </mu-container>
  </div>
</template>
<script>
import MaintenanceChild2 from "@/components/data/dataType";
import MaintenanceChildControl from "@/components/maintenanceChild3/index";
import MaintenanceChildInspection from "@/components/Inspection/index";
export default {
  name: "maintenance",
  data() {
    return {
      active2: 0,
      JstationName: "",
      Jtype: "",
      JtaskId: "",
      WstationName: "",
      Wtype: "",
      WtaskId: "",
      XstationName: "",
      Xtype: "",
      XtaskId: ""
    };
  },
  components: {
    MaintenanceChildControl,
    MaintenanceChild2,
    MaintenanceChildInspection
  },
  created() {
    if (this.$route.query.stationName !== undefined) {
      if (this.$route.query.type !== undefined) {
        switch (this.$route.query.type) {
          case "保养检修":
            this.active2 = 1;
            this.JstationName = this.$route.query.stationName;
            this.Jtype = this.$route.query.type;
            this.JtaskId = this.$route.query.taskId;
            break;
          case "故障维修":
            this.active2 = 2;
            this.WstationName = this.$route.query.stationName;
            this.Wtype = this.$route.query.type;
            this.WtaskId = this.$route.query.taskId;
            break;
          case "例行巡检":
            this.active2 = 0;
            this.XstationName = this.$route.query.stationName;
            this.Xtype = this.$route.query.type;
            this.XtaskId = this.$route.query.taskId;
            break;
        }
      }
    }
  }
};
</script>
<style scoped>
.header {
  position: fixed;
  top: 0;
}
.btnHeaderStyles {
  /* color: #ccc; */
}
.mu-ripple-wrapper {
  padding: 0 5px;
}
.ChildtabBtnStyles {
  width: 100%;
  /* border-bottom: #ccc 10px solid; */
  /* margin-bottom: 8px; */
  border-bottom: 5px solid #f1eff2;
  position: fixed;
  top: 46px;
}
</style>



