        value1: [],
        value2: "heart",
        value3: "disable"
      },
      reagentValue: {
        value1: [],
        value2: [],
        // value4:[],
        value3: false
      },
      // 管路清洗
      piping: {
        // value: [],
        value1: [],
        value2: [],
        // value4:[],
        value3: false
      },
      checkbox: {
        // value: [],
        value1: [],
        value2: [],
        // value4:[],
        value3: false
      },
      checkbox1: {
        // value: [],
        value1: [],
        value2: [],
        // value4:[],
        value3: false
      },
      checkbox2: {
        // value: [],
        value1: [],
        value2: false,
        // value4:[],
        value3: false
      },
      // runStatus:"",
      checkboxContent: [
        { id: 1, content: "标液标准" },
        { id: 2, content: "标准曲线" },
        { id: 3, content: "精密度检查" }
      ],
      checkboxContent1: [
        { id: 1, content: "仪器干净整洁" },
        { id: 2, content: "门窗正常关闭" },
        { id: 3, content: "分析仪器未阳光直射" },
        { id: 4, content: "空调" },
        { id: 5, content: "站房周围杂草、积水清除" },
        { id: 6, content: "防雷设施正常" },
        { id: 7, content: "漏雨检查" },
        { id: 8, content: "外部设备检查" },
        { id: 9, content: "其他" }
      ],
      RadioList: [
        { id: 1, content: "正常", color: "color: #4ed995;" },
        { id: 2, content: "异常", color: "color:#ef6c00" }
      ],
      checkboxContent: [
        { id: 1, content: "多参数在线分析仪" },
        { id: 2, content: "预处理系统" },
        { id: 3, content: "采样系统" },
        { id: 4, content: "质控系统" }
      ],
      checkboxContent3: [
        { id: 1, content: "泵管" },
        { id: 2, content: "接头" },
        { id: 3, content: "密封件" }
      ],
      checkboxContent4: [
        { id: 1, content: "定期添加" },
        { id: 2, content: "更换试剂" },
        { id: 3, content: "仪器标准" }
      ],
      checkboxContent5: [{ id: 1, content: "管路检查" }],
      // 耗材更换
      addListNumberHAOCAI: [
        {
          consumablesName: "",
          consumablesNum: "",
          consumablesModel: "",
          consumablesBrand: ""
        }
      ],
      // 管路清洗
      addPipingBtnList: [
        {
          pipingName: "",
          pipingNum: "",
          pipingModel: "",
          pipingBrand: ""
        }
      ],
      // 试剂更换
      reagent: [
        {
          reagentName: "",
          reagentNum: "",
          reagentModel: "",
          reagentBrand: ""
        }
      ],
      // 易损件更换
      addvulnerableListNumber: [
        {
          vulnerablePartsName: "",
          vulnerablePartsNum: 0,
          vulnerablePartsModel: "",
          vulnerablePartsBrand: ""
        }
      ],
      // 废液运输
      wasteShippingGroup: [
        {
          wasteName: "",
          wasteNumber: 0,
          wasteUnit: "",
          wasteVolume: ""
        }
      ],
      input: {
        inputValue1: "",
        inputValue2: "",
        inputValue3: "",
        inputValue4: "",
        inputValue5: [],
        inputValue6: [],
        inputValue7: "",
        inputValue8: "",
        inputValue9: ""
      },
      minHour: 10,
      maxHour: 20,
      minDate: new Date("1970-07-01"),
      maxDate: new Date(2050, 10, 1),
      currentDate: new Date(),
      DropDownCont: "",
      indexDrop: 0
      //   checkAll: true
    };
  },
  props: ["station", "type", "taskId"],
  methods: {
    // 废液删除
    removeWasteShipping() {
      this.wasteShippingGroup = AppBace.remove(this.wasteShippingGroup, 0);
    },
    // 废液外运
    addWasteShipping() {
      this.wasteShippingGroup = AppBace.add(this.wasteShippingGroup, "waste");
    },
    //Replacement 易损件更换
    Replacement(value, res) {
      var i = this.indexDrop;
      if (res.length !== undefined && res.length > 0) {
        for (let index = 0; index < res.length; index++) {
          var name = res[index].name;
          if (value === name) {
            this.addvulnerableListNumber[i].vulnerablePartsName =
              res[index].name;
            this.addvulnerableListNumber[i].vulnerablePartsNum =
              res[index].number;
            this.addvulnerableListNumber[i].vulnerablePartsModel =
              res[index].model;
            this.addvulnerableListNumber[i].vulnerablePartsBrand =
              res[index].brand;
          }
        }
      }
    },
    // consumables 耗材更换
    consumables(value, res) {
      var i = this.indexDrop;
      if (res.length !== undefined && res.length > 0) {
        for (let index = 0; index < res.length; index++) {
          var name = res[index].name;
          if (value === name) {
            this.addListNumberHAOCAI[i].consumablesName = res[index].name;
            this.addListNumberHAOCAI[i].consumablesNum = res[index].number;
            this.addListNumberHAOCAI[i].consumablesModel = res[index].model;
            this.addListNumberHAOCAI[i].consumablesBrand = res[index].brand;
          }
        }
      }
    },
    // Reagent 试剂更换
    Reagent(value, res) {
      var i = this.indexDrop;
      if (res.length !== undefined && res.length > 0) {
        for (let index = 0; index < res.length; index++) {
          var name = res[index].name;
          if (value === name) {
            this.reagent[i].reagentName = res[index].name;
            this.reagent[i].reagentNum = res[index].number;
            this.reagent[i].reagentModel = res[index].model;
            this.reagent[i].reagentBrand = res[index].brand;
          }
        }
      }
    },
    // Piping 管路清洗
    Piping(value, res) {
      var i = this.indexDrop;
      if (res.length !== undefined && res.length > 0) {
        for (let index = 0; index < res.length; index++) {
          var name = res[index].name;
          if (value === name) {
            this.addPipingBtnList[i].pipingName = res[index].name;
            this.addPipingBtnList[i].pipingNum = res[index].number;
            this.addPipingBtnList[i].pipingModel = res[index].model;
            this.addPipingBtnList[i].pipingBrand = res[index].brand;
          }
        }
      }
    },
    onChange(value) {
      this.showDropDown = false;
      if (this.arr !== []) {
        var res = this.arr;
        switch (this.DropDownCont) {
          case "易损件更换":
            this.Replacement(value, res);
            break;
          case "耗材更换":
            this.consumables(value, res);
            break;

          case "试剂更换":
            this.Reagent(value, res);
            break;
          case "管路清洗":
            this.Piping(value, res);
            break;
        }
      } else {
      }
    },
    // 下拉列表
    async selectList(e, name, index) {
      this.showDropDown = true;
      this.DropDownCont = e;
      this.indexDrop = index;
      var array = this.selectContent;
      var arr = [];
      var columns = [];
      var arrReagent = [];
      var columsReagent = [];
      for (let index = 0; index < array.length; index++) {
        var type = array[index].type;
        if (type === name) {
          arr.push(array[index]);
          columns.push(array[index].name);
        }
        // if (type==='试剂') {
        //   arrReagent.push(array[index]);
        //   columns.push(array[index].name);
        // }
      }
      // console.log(arr);
      this.arr = arr;
      // if (name==='其他') {
      this.columns = columns;
      // }
      // nameArr = [];
    },
    async initDataType(e) {
      var url = "/property/consumable?station=" + "固戍水质净化厂";
      var station = "";
      var res = await AppBace.request(
        url,
        "POST",
        {
          station
        },
        this.token
      );
      // console.log(res);
      this.selectContent = res.data.data;
      // this.$http
      //   .post(url, {
      //     station:station
      //   })
      //   .then(res => {
      //     console.log(res);
      //   })
      //   .catch(err => {});
    },
    confirmSubmit() {
      var _this = this;
      Dialog.confirm({
        title: "请确认是否提交数据？",
        message: "提交用户为：" + sessionStorage.getItem("username")
      })
        .then(() => {
          // on confirm
          _this.submit();
        })
        .catch(() => {
          // on cancel
          Toast("您点击了取消");
        });
    },
    selectDataPicStation() {
      // this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          // console.log(selectArr[0]);
          _this.initDataType(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          // _this.open = true;
        }
      });
    },
    // 时间选择器
    close() {
      this.open = false;
    },
    timeConfirm(value) {
      this.value3 = AppBace.filterTime(value);
      this.open = false;
    },
    timeSelect() {
      this.open = true;
    },
    closeBottomSheet() {
      this.open = false;
    },
    // 管路清洗
    addPipingBtn() {
      this.addPipingBtnList = AppBace.add(this.addPipingBtnList, "piping");
    },
    removePipingBtnList() {
      this.addPipingBtnList = AppBace.remove(this.addPipingBtnList, 0);
    },
    fullscreen() {
      this.loading = this.$loading();
    },
    // 试剂更换 移除
    removeListreagent() {
      this.reagent = AppBace.remove(this.reagent, 0);
    },
    // 试剂更换 新增
    addReagentBtn() {
      this.reagent = AppBace.add(this.reagent, "reagent");
    },
    getAddress() {
      //高德地图API
      //AMap.service解决只使用高德Geolocation功能提示Geolocation不是构造函数问题
      var _this = this;
      AMap.service(["AMap.Geolocation"], function() {
        _this.fullscreen();
        var geolocation = new AMap.Geolocation();
        geolocation.getCurrentPosition(function(status, result) {
          if (status == "complete") {
            _this.input.inputValue9 = result.formattedAddress;
            _this.loading.close();
          } else {
            _this.loading.close();
          }
        });
      });
    },
    addTitleBtn() {
      this.addvulnerableListNumber.push({
        vulnerablePartsCnt: "",
        vulnerablePartsRes: 1
      });
    },
    removeListNameNumber(index) {
      this.addvulnerableListNumber = AppBace.remove(
        this.addvulnerableListNumber,
        0
      );
    },
    addHAOCAIbtn() {
      this.addListNumberHAOCAI.push({
        consumablesName: "",
        consumablesNum: ""
      });
    },
    removeListNameNumberHaoCAI() {
      this.addListNumberHAOCAI = AppBace.remove(this.addListNumberHAOCAI, 0);
    },
    checkbox1Content(value) {
      console.log(this.checkbox1.value1);
    },
    async submit() {
      // 任务ID
      var taskId = this.taskIdCan;
      // 管路清洗
      var piping = this.piping.value1;
      var pipingGroup = this.addPipingBtnList;
      var pipingRes = this.radio1111.value1;
      //  试剂更换
      var reagent = this.reagentValue.value1;
      var reagentGroup = this.reagent;
      var reagentRes = this.radio111.value1;
      // 站房环境监检查
      var stationAmbient = this.checkbox.value1;
      var stationAmbientCnt = this.input.Inspectioncontent;
      var stationAmbientRes = this.input.InspectionResult;
      // 易损件更换
      var vulnerableParts = this.addvulnerableListNumber;
      // 耗材更换
      var consumables = this.checkbox2.value1;
      var consumablesGroup = this.addListNumberHAOCAI;
      var consumablesRes = this.Consumables.value1;
      // 仪器与系统检查
      var appSystem = this.checkbox1.value1;
      // 空压机
      var appSystemAirPump = this.Aircompressor.value1;
      // 操作内容
      var appSystemCnt = this.input.textvalueMainContent;
      // 工控机
      var appSystemIPC = this.IPC.value1;
      // 供电
      var appSystemPower = this.radioPower.value1;
      // 运行状态
      var appSystemRunState = this.radio.value1;
      // 传感器
      var appSystemStirMotor = this.sensor.value1;
      // 温度
      var appSystemTemper = this.temperature.value1;
      // 搅拌电机
      var appSystemTemperSensor = this.Stirringmotor.value1;
      // 采水系统
      var appSystemWaterExtraction = this.Waterextractionsystem.value1;
      // 废液运输
      var wasteShippingGroup = this.wasteShippingGroup;
      var content = {
        piping,
        pipingGroup,
        pipingRes,
        reagent,
        reagentGroup,
        reagentRes,
        stationAmbient,
        stationAmbientCnt,
        stationAmbientRes,
        vulnerableParts,
        consumables,
        consumablesGroup,
        consumablesRes,
        appSystem,
        appSystemAirPump,
        appSystemCnt,
        appSystemIPC,
        appSystemPower,
        appSystemRunState,
        appSystemStirMotor,
        appSystemTemper,
        appSystemTemperSensor,
        appSystemWaterExtraction,
        wasteShippingGroup
      };
      // debugger;
      var time = AppBace.filterTime(this.value3);

      var station = this.normal.value1;
      var person = this.controlMaintainValue;
      var remark = this.otherValueRemark;
      var type = "inspect";
      // 上传请求
      var url = "/job/operate";
      var res = await AppBace.request(
        url,
        "POST",
        {
          taskId,
          content,
          time,
          station,
          person,
          remark,
          type
        },
        this.token
      );
      console.log(res);
      if (res.data.message === "success") {
        this.$router.push({
          path: "/controlList"
        });
        Toast(res.data.info);
      } else {
        Toast(res.data.info);
      }
    }
  },
  created() {
    this.token = sessionStorage.getItem("token");
    if (this.station !== "" && this.station !== undefined) {
      this.taskIdCan = this.taskId;
      this.normal.value1 = this.station;
      this.initDataType(this.normal.value1);
    }

    this.options = this.$store.state.menu;

    this.controlMaintainValue = sessionStorage.getItem("username");
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
@import "../../../static/css/dropDown.css";
@import "../../../static/css/inspect.css";
.imageDrop img {
  width: 20px;
  height: 20px;
}
.imageDrop {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 30px;
  padding: 2px;
  /* flex: 1; */
  width: 45%;
}
.dropDownInput {
  border: none;
  background-color: white;
  width: 90%;
}
.dropDownFlex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 40px;
}
.vanFlex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 45%;
}
.btnAdd {
  padding: 3px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: rgb(53, 114, 254);
  color: rgb(255, 255, 255);
  margin: 0 10px;
}
.btnRemove {
  padding: 3px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  color: rgb(121, 121, 121);
}
.margin-top{
  margin-top: 10px;
}
.margin-bottom{
  /* margin-bottom: 10px; */
}
</style>



