<template>
  <mu-col style="padding:10px;background:#f1eff2;margin-top: 80px;">
    <div class="containerMain">
      <div class="flexContainer">
        <div class="titleselect">
          <span>水站名称：</span>
        </div>
        <div class="select" id="classPicstation" @click="selectDataPicStation">
          <input type="text" placeholder="请选择当前站点" class="input" disabled v-model="normal.value1" />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>时间：</span>
        </div>
        <div class="select" @click="timeSelect">
          <input
            type="text"
            class="input"
            v-model="value3"
            style="background:white"
            disabled
            placeholder="选择录入的时间"
          />
        </div>
      </div>
    </div>
    <div class="containerMain">
      <div class="flexContainer">
        <div class="titleselect">
          <span>仪器名称：</span>
        </div>
        <div class="select">
          <input type="text" placeholder="请输入检修仪器的名称" v-model="input.inputValue1" class="input" />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>规格型号：</span>
        </div>
        <div class="select">
          <input
            type="text"
            class="input"
            v-model="input.inputValue2"
            style="background:white"
            placeholder="请输入规格型号"
          />
        </div>
      </div>
    </div>
    <div class="containerMain">
      <div class="titleselect titleselectNews">
        <span>故障原因：</span>
      </div>
      <div class="select borderNo">
        <textarea name id="textvalueMain" v-model="input.inputValue3" cols="40" rows="10"></textarea>
      </div>

      <div class="titleselect titleselectNews margin-top">
        <span>解决措施：</span>
      </div>
      <div class="select borderNo">
        <textarea name id="textvalueMain" v-model="input.inputValue4" cols="40" rows="10"></textarea>
      </div>
    </div>
    <div class="containerMain" v-for="(item,index) in listContent" :key="index">
      <div class="flexContainer">
        <div class="titleselect">
          <span>更换内容：</span>
        </div>
        <div class="select">
          <input type="text" class="input" placeholder="请输入更换内容" v-model="item.replaceCnt" />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>更换数量：</span>
        </div>
        <div class="select">
          <input
            type="text"
            class="input"
            placeholder="请输入更换内容数量，（阿拉伯数字）"
            v-model="item.replaceNum"
          />
        </div>
      </div>
      <button class="btnStyles backgroundNaviBar" @click="addClickBtn()">新增</button>
      <button class="btnStyles" @click="removeClickBtn()">移除</button>
    </div>

    <div class="containerMain">
      <div class="titleselect">
        <span>仪器标准：</span>
      </div>
      <div class="select borderNo">
        <mu-flex class="select-control-row" :key="index" v-for="(item,index) in checkboxContent">
          <mu-checkbox
            :value="item.content"
            v-model="checkbox.value1"
            @click="checkboxContentBtn(index)"
            :label="item.content"
          ></mu-checkbox>
        </mu-flex>
      </div>

      <div class="titleselect titleselectNews margin-topTwo">
        <span>备注：</span>
      </div>
      <div class="select borderNo">
        <textarea name id="textvalueMain" cols="30" rows="10" v-model="input.inputValue7"></textarea>
      </div>
    </div>

    <div class="containerMain">
      <div class="flexContainer">
        <div class="titleselect">
          <span>地址：</span>
        </div>
        <div class="select">
          <input type="text" class="input" disabled v-model="input.inputValue9" />
        </div>
        <div>
          <button class="btnStylesAddress" @click="getposition()">获取地址</button>
        </div>
      </div>
    </div>
    <!-- <mu-col span="8" offset="2" style="margin-bottom:80px;margin-top:20px;">
      <mu-button
        style="height:40px;"
        full-width
        :color="$store.state.color"
        @click="confirmSubmit"
      >提交</mu-button>
    </mu-col> -->
    <div class="bottomSubmit">
      <div class="btnSubmit backgroundNaviBar" :style="'background:'+$store.state.color" @click="confirmSubmit()">
        <span>提交</span>
      </div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-sub-header>选择时间</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="datetime"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="timeConfirm"
            @cancel="close"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </mu-col>
</template>
<script>
import AppBace from "../../../static/ajax";
// import GAODE from "../../../static/js/geocoder";
import Coordtransform from "../../../static/js/conmonGogeChange";

import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Dialog } from "vant";
import "vant/lib/dialog/style";
// Vue.use(Dialog);
export default {
  name: "maintenanceChild3",
  data() {
    return {
      listContent: [{ replaceCnt: "", replaceNum: "" }],
      options: [],
      value3: null,
      normal: {
        value1: "",
        value2: null
      },
      checkbox: {
        value1: [],
        value2: false,
        value3: false
      },
      checkboxContent: [
        { id: 1, content: "标液校准" },
        { id: 2, content: "标准曲线" },
        { id: 3, content: "精密度检查" }
      ],
      input: {
        inputValue1: "",
        inputValue2: "",
        inputValue3: "",
        inputValue4: "",
        inputValue5: [],
        inputValue6: [],
        inputValue7: "",
        inputValue8: "",
        inputValue9: ""
      },
      checkAll: true,
      loading: false,
      token: "",
      msg: "",
      open: false,
      minHour: 10,
      maxHour: 20,
      minDate: new Date("1970-07-01"),
      maxDate: new Date(2050, 10, 1),
      currentDate: new Date()
    };
  },
  methods: {
    confirmSubmit() {
      var _this = this;
      Dialog.confirm({
        title: "请确认是否提交数据？",
        message: "提交用户为：" + this.input.inputValue8
      })
        .then(() => {
          // on confirm
          _this.submitData();
        })
        .catch(() => {
          // on cancel
          Toast("您点击了取消");
        });
    },
    selectDataPicStation() {
      // this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          // _this.open = true;
        }
      });
    },
    timeConfirm(value) {
      this.value3 = AppBace.filterTime(value);
      this.open = false;
    },
    close() {
      this.open = false;
    },
    closeBottomSheet() {
      this.open = false;
    },
    timeSelect() {
      this.open = true;
    },
    getposition: function(val) {
      var _this = this;

      navigator.vibrate(500);
      var me = {
        msg: ""
      };
      var onSuccess = function(position) {
        console.log("position", position);
        var lat = "";
        var lng = "";
        var wgs84togcj02 = Coordtransform.wgs84togcj02(
          Number(position.coords.longitude),
          Number(position.coords.latitude)
        );
        console.log(wgs84togcj02);
        _this.getGaoDE(wgs84togcj02[0], wgs84togcj02[1]);
        //   return arr;
      };
      var error = function(error) {
        me.msg =
          "code: " + error.code + "\n" + "message: " + error.message + "\n";

        alert(me.msg);
        console.log(me.msg);
      };

      navigator.geolocation.getCurrentPosition(onSuccess, error, {
        maximumAge: 30000,
        timeout: 30000,
        enableHighAccuracy: true
      });
    },
    getGaoDE(e, f) {
      var geocoder;
      var that = this;
      var lnglatXY = new AMap.LngLat(Number(e), Number(f));
      //加载地理编码插件
      AMap.plugin(["AMap.Geocoder"], function() {
        geocoder = new AMap.Geocoder({
          radius: 1000, //以已知坐标为中心点，radius为半径，返回范围内兴趣点和道路信息
          extensions: "all" //返回地址描述以及附近兴趣点和道路信息，默认"base"
        });
        //返回地理编码结果
        AMap.event.addListener(geocoder, "complete", geocoder_callBack);
        //逆地理编码
        geocoder.getAddress(lnglatXY);
      });
      function geocoder_callBack(params) {
        console.log("params", params);
        that.input.inputValue9 = params.regeocode.formattedAddress;
        // alert("当前" + params.regeocode.formattedAddress);
        // alert("状态" + params.info);
        // alert(params.type);
        // alert("数组" + params.regeocode, addressComponent);
      }
    },

    //  android的定位服务
    getLoacal() {
      var config = {
        // android配置
        interval: 2000, // 连续定位时间间隔（ms）
        androidAccuracy: 1, // 精确度 0. Battery_Saving 1. Hight_Accuracy, 2. Device_Sensors
        needAddress: true // 是否返回地址信息
      };
      window.AmapLocation.getCurrentPosition(
        config,
        x => {
          console.log(x);
          alert(x);
        },
        e => {
          console.error(e);
          alert("失败" + e);
        }
      );
    },

    fullscreen() {
      this.loading = this.$loading();
    },
    addClickBtn() {
      this.listContent.push({
        content: "",
        number: ""
      });
    },
    removeClickBtn() {
      this.listContent.splice(0, 1);
    },
    checkboxContentBtn(value) {
      console.log(this.checkbox.value1);
    },
    async submitData() {
      var url = "/job/operate";
      var type = "repair";
      var person = this.input.inputValue8;
      var remark = this.input.inputValue7;
      var station = this.normal.value1;
      var address = this.input.inputValue9;
      var deviceName = this.input.inputValue1;
      var modelType = this.input.inputValue2;
      var failureReason = this.input.inputValue3;
      var replaceCnt = "";
      var replaceNum = "";
      var solutions = this.input.inputValue4;
      var replaceGroup = this.listContent;
      var calibration = this.checkbox.value1;
      var time = AppBace.filterTime(this.value3);
      var content = {
        address,
        deviceName,
        modelType,
        failureReason,
        solutions,
        calibration,
        replaceGroup
        // replaceCnt,
        // replaceNum
      };
      var res = await AppBace.request(
        url,
        "POST",
        {
          type,
          person,
          remark,
          station,
          time,
          content
        },
        this.token
      );
      console.log(res);
      if (res.data.message === "success") {
        Toast(res.data.info);
        this.$router.replace("./controlList");
      } else {
        Toast(res.data.info);
      }
    }
  },
  // navigator.geolocation.getCurrentPosition(success, error, options)
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.options = this.$store.state.menu;
    this.input.inputValue8 = sessionStorage.getItem("username");
    // var bd09togcj02 = coordtransform.bd09togcj02(116.404, 39.915);
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
@import "../../../static/css/maintain.css";
.contanerMain {
  margin: 0 5px;
  background: white;
  padding: 10px;
  border-radius: 10px;
  margin-top: 103px;
  /* margin-bottom: 50px; */
}
.flexCenterMain {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  border-bottom: 1px #f1eff2 solid;
}
.flexCenterMainChildTextValue {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
}
#textvalueMain {
  height: 80px;
  width: 100%;
  border: none;
  border-radius: 5px;
  border: 1px solid #ccc;
  outline: none;
}
.MainList {
  border-bottom: 1px solid rgb(243, 243, 243);
  height: 40px;
  display: flex;
  /* align-items: center; */
  padding-top: 9px;
  align-content: center;
  /* justify-content: center; */
}
.select-control-row {
  padding: 8px 0;
  color: blue;
  display: flex;
  flex-direction: column;
}
.select-control-group {
  margin: 16px 0;
  /* color: blue; */
}
#input {
  /* widows: 100%; */
  width: 100%;
  outline: none;
  border: none;
  border-radius: 5px;
  height: 30px;
}
.btnStyles {
  border: none;
  border: #ccc 1px solid;
  border-radius: 5px;
  background: #f1eff2;
  margin-top: 10px;
  outline: none;
}
.contanerMain1 {
  margin: 0 5px;
  background: white;
  padding: 10px;
  border-radius: 10px;
  margin-top: 10px;
}
.btnStylesAddress {
  border: none;
  width: 100px;
  border: #ccc 1px solid;
  border-radius: 5px;
  background: #f1eff2;
  margin-top: 2px;
  margin-left: 5px;
  outline: none;
}
.spanCenterContent {
  display: flex;
  height: 30px;
  align-items: center;
}
.borderNo{
  border: none;
}
.titleselectNews{
  margin: 0px 0 10px 0;
}
.margin-top{
  margin-top: 10px;
}
.margin-topTwo{
  margin-top: 2px;
}
</style>



