<template>
  <mu-container style="padding:10px;background:#f1eff2">
    <div class="containerMain">
      <div class="flexContainer">
        <div class="titleselect">
          <span>任务编号：</span>
        </div>
        <div class="select" id="classPicstationTask">
          <input
            :disabled="taskId===undefined?false:true"
            type="text"
            placeholder="请填写任务编号"
            class="input"
            v-model="taskIdCan"
          />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>水站名称：</span>
        </div>
        <div class="select" id="classPicstation" @click="selectDataPicStation">
          <input type="text" placeholder="请选择当前站点" class="input" v-model="normal.value1" disabled />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>时间：</span>
        </div>
        <div class="select" @click="timeSelect">
          <input
            type="text"
            class="input"
            v-model="value3"
            style="background:white"
            disabled
            placeholder="选择录入的时间"
          />
        </div>
      </div>
    </div>
    <div class="containerMain">
      <div class="flexContainer">
        <div class="titleselect">
          <span>仪器名称：</span>
        </div>
        <div class="select">
          <input type="text" class="input" v-model="maintainNameValue" placeholder="填写仪器名称" />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>规格型号：</span>
        </div>
        <div class="select">
          <input type="text" class="input" v-model="Specificationtype" placeholder="填写规格型号" />
        </div>
      </div>
    </div>
    <div class="containerMain paddingMain">
      <div class="select-control-group flexGroupInspection">
        <div style>检修类型：</div>
        <mu-flex class="select-control-row" :key="index" v-for="(item,index) in checkboxContent">
          <mu-radio :value="item.content" v-model="radio.value1" :label="item.content"></mu-radio>
        </mu-flex>
      </div>
    </div>
    <div class="containerMain">
      <div class="flexContainer">
        <div class="titleselect">
          <span>检修结果：</span>
        </div>
        <div class="select">
          <input type="text" class="input" v-model="controlResult" placeholder="填写检修结果" />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>本次检修：</span>
        </div>
        <div class="select" @click="selectthisTime">
          <input type="text" class="input" v-model="value4" disabled placeholder="选择本次检修的时间" />
        </div>
      </div>
      <div class="flexContainer">
        <div class="titleselect">
          <span>上次检修：</span>
        </div>
        <div class="select" @click="selectLastTime">
          <input type="text" class="input" v-model="value5" disabled placeholder="选择上次检修的时间" />
        </div>
      </div>
    </div>
    <div class="containerMain">
      <div class="flexContainer">
        <div class="titleselect">
          <span>地址：</span>
        </div>
        <div class="select">
          <input type="text" class="input" disabled v-model="input.address" placeholder="获取当前地址" />
        </div>
      </div>
      <div class="margin-bottom">
        <span>其他：</span>
      </div>
      <div>
        <textarea name id="textvalueMain" cols="40" rows="10" v-model="textvalueMain"></textarea>
      </div>
    </div>
    <!-- <mu-col span="8" offset="2" style="margin-bottom:80px;margin-top:20px;">
      <mu-button
        style="height:40px;"
        full-width
        :color="$store.state.color"
        @click="confirmSubmit"
      >提交</mu-button>
    </mu-col>-->
    <div class="bottomSubmit">
      <div class="btnSubmit backgroundNaviBar" :style="'background:'+$store.state.color" @click="confirmSubmit()">
        <span>提交</span>
      </div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-sub-header>选择时间</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="datetime"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="timeConfirm"
            @cancel="close"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </mu-container>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Dialog } from "vant";
import "vant/lib/dialog/style";
export default {
  name: "maintenanceChild3",
  data() {
    return {
      taskIdCan: "",
      personInput: "",
      textvalueMain: "",
      controlResult: "",
      Specificationtype: "",
      maintainNameValue: "",
      input: {
        inputValue8: "",
        inputValue7: "",
        address:""
      },
      options: ["沙井一期进水口水质监测站"],
      value3: null,
      value4: null,
      value5: null,
      normal: {
        value1: "",
        value2: null
      },
      radio: {
        value1: [],
        value2: false,
        value3: false
      },
      checkboxContent: [
        { id: 1, content: "实验室检修" },
        { id: 2, content: "现场检修" }
        // { id: 3, content: "精密度检查" }
      ],

      checkAll: true,
      token: "",
      open: false,
      minHour: 10,
      maxHour: 20,
      minDate: new Date("1970-07-01"),
      maxDate: new Date(2050, 10, 1),
      currentDate: new Date(),
      time: 0
    };
  },
  props: ["station", "type", "taskId"],
  methods: {
    confirmSubmit() {
      var _this = this;
      Dialog.confirm({
        title: "请确认是否提交数据？",
        message: "提交用户为：" + this.input.inputValue8
      })
        .then(() => {
          // on confirm
          _this.submitData();
        })
        .catch(() => {
          // on cancel
          Toast("您点击了取消");
        });
    },
    selectDataPicStation() {
      // this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          // _this.open = true;
        }
      });
    },
    selectLastTime() {
      this.open = true;
      this.time = 2;
    },
    selectthisTime() {
      this.open = true;
      this.time = 1;
    },
    // 时间选择器
    close() {
      this.open = false;
    },
    timeConfirm(value) {
      var time = AppBace.filterTime(value);
      switch (this.time) {
        case 0:
          this.value3 = time;
          break;
        case 1:
          this.value4 = time;
          break;
        case 2:
          this.value5 = time;
          break;
      }
      // this.value3 = AppBace.filterTime(value);
      this.open = false;
    },
    timeSelect() {
      this.time = 0;
      this.open = true;
    },
    closeBottomSheet() {
      this.open = false;
    },
    async submitData() {
      var url = "/job/operate";
      var type = "maintenance";
      var person = this.input.inputValue8;
      var remark = this.textvalueMain;
      var station = this.normal.value1;
      var deviceName = this.maintainNameValue;
      var modelType = this.Specificationtype;
      var maintenType = this.radio.value1;
      var OverhaulRes = this.controlResult;
      var thisTime = AppBace.filterTime(this.value4);
      var LastTime = AppBace.filterTime(this.value5);
      var otherContent = this.textvalueMain;
      var xiaoxie = "";
      var pageNum = 1;
      var pageSize = 100000000;
      // var time = this.value3;
      var time = AppBace.filterTime(this.value3);
      // console.log(time);
      var content = {
        deviceName,
        modelType,
        maintenType,
        OverhaulRes,
        thisTime,
        LastTime
      };

      var res = await AppBace.request(
        url,
        "POST",
        {
          type,
          person,
          remark,
          station,
          time,
          content,
          pageSize,
          pageNum
        },
        this.token
      );
      console.log(res);
      var message = res.data.message;
      var info = res.data.info;
      if (message === "success") {
        Toast(info);
        this.$router.push("/controlList");
      } else {
        Toast(info);
      }
    }
  },
  mounted() {
    this.options = this.$store.state.menu;
    this.token = sessionStorage.getItem("token");
    this.input.inputValue8 = sessionStorage.getItem("username");
    if (this.station !== "" && this.station !== undefined) {
      this.taskIdCan = this.taskId;
      this.normal.value1 = this.station;
    }
    //   console.log("path", this.$router.history.current.path);
    //   console.log(this.checkbox.value1);
  }
};
</script>
<style scoped>
@import "../../../static/css/maintain.css";
@import "../../../static/css/model.css";
.contanerMain {
  margin: 0 5px;
  background: white;
  margin-top: 103px;
  padding: 10px;
  border-radius: 10px;
  /* margin-bottom: 50px; */
}
.flexCenterMain {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  border-bottom: 1px #f1eff2 solid;
}
.flexCenterMainChildTextValue {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
}
#textvalueMain {
  height: 80px;
  width: 100%;
  border: none;
  border-radius: 5px;
  outline: none;
  border: 1px solid #ccc;
}
.MainList {
  border-bottom: 1px solid rgb(243, 243, 243);
  height: 40px;
  display: flex;
  /* align-items: center; */
  align-content: center;
  padding-top: 10px;
  /* justify-content: center; */
}
.select-control-row {
  padding: 8px 0;
  color: #ccc;
  display: flex;
  flex-direction: column;
}
.select-control-group {
  margin: 16px 0;
  /* color: blue; */
}
#input {
  /* widows: 100%; */
  outline: none;
  border: none;
}
.paddingMain{
  padding: 1px 10px;
}
.margin-bottom{
  margin-bottom: 10px;
}
</style>




